        Select:\n              <input type=\"radio\" v-model=\"modalUser.userType\" value=\"0\" />\n              Student\n              <input type=\"radio\" v-model=\"modalUser.userType\" value=\"1\" />\n              Recruiter\n            </label>\n            <br />\n            <label>\n              Name:\n              <input type=\"text\" v-model=\"modalUser.name\" class=\"form-control\" required />\n            </label>\n            <label>\n              Email Address:\n              <input\n                type=\"email\"\n                v-model=\"modalUser.email\"\n                class=\"form-control\"\n                required\n              />\n            </label>\n            <label>\n              Password:\n              <input\n                type=\"password\"\n                v-model=\"modalUser.password\"\n                class=\"form-control\"\n                required\n              />\n            </label>\n            <label v-if=\"modalUser.userType === '1'\">\n              Company Name:\n              <input\n                type=\"text\"\n                v-model=\"modalUser.company\"\n                class=\"form-control\"\n                required\n              />\n            </label>\n            <br />\n            <br />\n            <div align=\"center\">\n              <button type=\"submit\" class=\"btn btn-success\">Register</button>\n            </div>\n          </form>\n        </div>\n      </div>\n    </VuexplosionModal>\n\n    <!-- Modal for User LogIn -->\n    <VuexplosionModal :visible=\"showLogin\" @close=\"closeLogin\">\n      <div slot=\"title\">Log In</div>\n      <div slot=\"body\">\n        <div class=\"register-modal\">\n          <form @submit=\"handleLogin\">\n            <br />\n            <label>\n              Email Address:\n              <input\n                type=\"email\"\n                v-model=\"modalUser.email\"\n                class=\"form-control\"\n                required\n              />\n            </label>\n            <label>\n              Password:\n              <input\n                type=\"password\"\n                v-model=\"modalUser.password\"\n                class=\"form-control\"\n                required\n              />\n            </label>\n            <br />\n            <br />\n            <div align=\"center\">\n              <button type=\"submit\" class=\"btn btn-success\">Log In</button>\n            </div>\n          </form>\n        </div>\n      </div>\n    </VuexplosionModal>\n  </div>\n</template>\n\n<script>\nimport VuexplosionModal from './../custom/VueModal';\nimport {mapGetters} from 'vuex';\n\nexport default {\n  name: 'HeaderHome',\n  components: {\n    VuexplosionModal\n  },\n  data() {\n    return {\n      showSignup: false,\n      showLogin: false,\n      modalUser: {\n        userType: '0'\n      }\n    };\n  },\n  computed: {\n    ...mapGetters({\n      isAuth: 'getUserAuth'\n    })\n  },\n  methods: {\n    handleSignup(e) {\n      e.preventDefault();\n      this.$store.dispatch('registerUser', this.modalUser);\n      this.closeSignup();\n    },\n    handleLogin(e) {\n      e.preventDefault();\n      this.$store.dispatch('loginUser', this.modalUser).then(userType => {\n        console.log(userType);\n        if (userType === 0) {\n          this.$router.push({path: '/jobs'});\n        } else {\n          this.$router.push({path: '/recruit'});\n        }\n      });\n      this.closeLogin();\n    },\n    openLogin() {\n      console.log(this);\n      this.showLogin = !this.showLogin;\n    },\n    closeLogin() {\n      this.showLogin = false;\n      this.modalUser = {userType: '0'};\n    },\n    openSignup() {\n      this.showSignup = !this.showSignup;\n    },\n    closeSignup() {\n      this.showSignup = false;\n      this.modalUser = {userType: '0'};\n    },\n    logout() {\n      this.$store.dispatch('logout');\n    }\n  }\n};\n</script>\n\n<style scoped>\nheader {\n  background-color: rgb(52, 28, 58);\n 