             class=\"form-control\"\n                required\n                rows=\"8\"\n                cols=\"19\"\n              />\n            </label>\n            <br />\n            <br />\n            <div align=\"center\">\n              <button type=\"submit\" class=\"btn btn-success\">Submit</button>\n            </div>\n          </form>\n        </div>\n      </div>\n    </VuexplosionModal>\n\n    <!-- Modal for Adding New Post/ Editing Post -->\n    <VuexplosionModal :visible=\"showApplicants\" @close=\"closeApplicants\">\n      <div slot=\"title\">\n        Job Applicants\n      </div>\n      <div slot=\"body\">\n        <div class=\"add-modal\">\n          <table class=\"table\">\n            <tr>\n              <th>Name</th>\n              <th>Email</th>\n            </tr>\n            <tr v-for=\"application in jobApplicants\" :key=\"application._id\">\n              <td v-text=\"application.candidateId.name\"></td>\n              <td v-text=\"application.candidateId.email\"></td>\n            </tr>\n          </table>\n        </div>\n      </div>\n    </VuexplosionModal>\n  </div>\n</template>\n\n<script>\nimport VuexplosionModal from './custom/VueModal';\nimport HeaderHome from './headers/HeaderHome';\nimport { mapGetters } from 'vuex';\n\nexport default {\n  name: 'RecruitListing',\n  components: { VuexplosionModal, HeaderHome },\n  data() {\n    return {\n      showAddPost: false,\n      showApplicants: false,\n      modalPost: {}\n    };\n  },\n  computed: {\n    ...mapGetters({\n      recruiterListing: 'getRecruiterList',\n      jobApplicants: 'getJobApplicants'\n    })\n  },\n  mounted() {\n    this.$store.dispatch('getRecruiterListing');\n  },\n  methods: {\n    openAddPost() {\n      this.showAddPost = !this.showAddPost;\n    },\n    closeAddPost() {\n      this.showAddPost = false;\n      this.modalPost = {};\n    },\n    handleAddPost(e) {\n      e.preventDefault();\n      if (this.modalPost._id) {\n        this.$store.dispatch('editJobPost', this.modalPost);\n      } else {\n        this.$store.dispatch('addJobPost', this.modalPost);\n      }\n      this.closeAddPost();\n    },\n    openPostEdit(post) {\n      this.modalPost = Object.assign({}, post);\n      this.openAddPost();\n    },\n    openApplicants(jobId) {\n      this.$store.dispatch('getJobApplicants', jobId);\n      this.showApplicants = !this.showApplicants;\n    },\n    closeApplicants() {\n      this.showApplicants = false;\n      this.jobApplicants = [];\n    }\n  }\n};\n</script>\n\n<style scoped>\n.add-modal {\n  width: 400px;\n}\nlabel {\n  width: 90%;\n}\n.listing-card {\n  margin: 45px 100px;\n  box-shadow: 0px 1px 20px rgba(0, 0, 0, 0.05);\n  border-radius: 8px;\n  width: 85%;\n  padding: 40px 40px 10px 40px;\n}\n.job-card {\n  background: rgba(129, 134, 163, 0.05);\n  border-radius: 8px;\n  min-height: 140px;\n  margin-bottom: 40px;\n}\n.job-title {\n  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n  font-size: 1.6em;\n  display: inline-block;\n  color: #222222;\n}\n.flex-align {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  padding: 15px 20px 0 20px;\n}\n.job-description {\n  padding: 10px 20px;\n  color: #555555;\n}\n.applicant-info {\n  padding: 10px 20px;\n  color: #3161a8;\n  font-size: 0.9em;\n  cursor: pointer;\n}\n.title {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 25px;\n}\n</style>\n"],"sourceRoot":""}]);

